 async function  bindleftCommonSearchdropdown(instance,modulename, filtername) {
    var vm = instance;
    let params = {};
    params["moduleName"] = modulename; //"CRM";
    params["type"] = filtername
   await AccountDataServices.GetLeftSearchStatus(params)
        .then(function (response) {
        var json = response.data;
            if (json != null) {
                if (filtername == "clientAccType") {
                    vm.leftSearchFields[1].listOptions = json.DATA;
                    vm.LeadStatusList = json.DATA;
                    return vm.LeadStatusList;
                }
                if (filtername == "CRMACCOUNTS") {
                    vm.leftSearchFields[2].listOptions = json.DATA;
                    vm.LeadOwnerList = json.DATA
                    return vm.LeadOwnerList;
                }
            }
        });
}
export default { buttons, leftsearchSchema, bindleftCommonSearchdropdown,listActions };