<script>
    import LeadDataService from "../../../services/DataService";
export default {
    data() {
            return {
                GetAccountType: [],
                SelectedValue:1
            }
        },
        created: function() {
            this.FetchData();
        },
        methods: {
            RedirectAccount: function () {
                var selected = this.SelectedValue;
                var AccountCode = '';
                $.each(this.GetAccountType, function (index, data) {
                    if (data.value == selected) {
                        AccountCode = data.account_code;
                    }
                })
                this.$router.push({name:'CRMManageAccount',query:{
                    id:0,
                    AccountId:selected,
                    AccountCode:AccountCode
                }});
            },
            FetchData: function () {    
                var vm=this;         
                LeadDataService.GetAccountTypes().then(function (response) {
                    var data = response.data.DATA;
                    vm.GetAccountType = data;
                })
            }
        } 
}
</script>
<template>
     <transition name="modal">
        <div class="modal d-block my-popups dialog">
            <div class="modal-dialog">
                <div class="modal-content" >
                    <div class="modal-header">
                        <slot name="header">
                            <h5 class="modal-title">Select Account Type</h5>
                        </slot><button type="button" class="close" v-on:click="$emit('close')"
                            data-original-title="Close"><em class="fa fa-times"></em></button>
                    </div>
                    <div class="modal-body" style="max-height: 70vh;overflow: auto;" id="dialogAddAccountType">
                        <div class="container-fluid" id="divMainAccountType" v-cloak>
            <form class="row">
                <div class="col-md-12  p-0">
                    <div class="custom-radio mb-2" v-for="(option,index) in GetAccountType" :key="index">
                        <div class="form-check custom-radio">
                            <input class="dynamic form-check-input custom-control-input" v-model="SelectedValue" v-bind:data-resource="option.name" name="AccountType" type="radio" v-bind:id="'rdo_' + option.value" v-bind:value="option.value">
                            <label class="custom-control-label universalradio-custom-control-label pl-0 pt-0 dynamic" v-bind:for="'rdo_' + option.value">{{option.name}}</label>
                        </div>
                    </div>
                </div>
                <div class="col-md-12 text-right  p-0">
                    <a title="Next" class="btn btn-primary" v-on:click="RedirectAccount"><em class="fa fa-forward pr-2"></em>Next</a>
                </div>
            </form>
        </div>
                      
                    </div>
                </div>

            </div>
        </div>
    </transition>
</template>