
<template>
  <section>
  <loader :is-visible="isLoading"></loader>
    <TgList
      IsShowAction="true"
      ModuleName="CRM"
      SubModuleCode="CRM_ACCOUNTS"
      IdentityColumn="USER_ID"
      :HeaderText="$t('Accounts')"
      :ListData="AccountsData"
      :HeaderData="Headers"
      :HeaderButtons="listheaderbuttons"
      :TotalRecords="TotalRecords"
      :IsSearchFieldsOnTop="false"
      :SearchFields="leftSearchFields"
      :RenderRowActionMethod="renderActions"
      :ListDataCallBackFunction="FetchData"
      :LegendArray="LegendArray"
      :IsFilterApplied="searchCondition?true:false"
      :SortExp="SortExp"
      :SortBy="SortBy"
      :CurrentPageNumber="PageNumber"
      v-on:LeftsearchButtonClick="leftCommonSearch"
      v-on:PagerButtonClick="pagerMethod"
      v-on:HeaderButtonClick="commonHeaderButtonClick"
      v-on:ActionButtonClick="actionButtonClick"
      v-on:SortdataButtonClick="sortdata"
    >
      <template v-slot:slotdata="{ data }">  
        <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">        
           <span title="" v-if="data.row.STATUS_ID==1002" class="px-2 bg-danger text-center text-white d-block">{{data.row.STATUS_NAME}}</span>
           <span title="" v-else-if="data.row.STATUS_ID==1001" class="px-2 bg-success text-center text-white d-block">{{data.row.STATUS_NAME}}</span>
           <span title="" v-else-if="data.row.STATUS_ID==12506" class="px-2 bg-warning text-center text-white d-block">{{data.row.STATUS_NAME}}</span>
                <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'CRM_ACCOUNTS')">
                    <span v-bind:class="getStatusClass(data.row.status_id, 'STATUS_ID')">
                        {{data.row[data.column.COLUMN_NAME]}}
                    </span>
                </a>
            </template>  
      </template>
    </TgList>
    <openaddaccountmodal v-if="showAccountPopup"  @close="showAccountPopup=false"></openaddaccountmodal> 
  </section>
   
</template>

<script>
import Schema from "./AccountsListingSchema";
import AccountsDataServices from "../../../services/DataService";
import openaddaccountmodal from '../../../components/Common/Modal/openaddaccountmodal.vue'
import TgList from "../../../components/Common/TgList.vue";
import $ from "jquery";
export default {
  components: {
    TgList,
    openaddaccountmodal
  },
  data() {
    return {
      isLoading: false, 
      leftSearchFields: Schema.leftsearchSchema,
      listheaderbuttons: Schema.buttons,
      AccountsData: null,
      StatusListForListing: [],
   
      Headers: [ 
        {
          COLUMN_NAME: "CLIENT_NAME",
          DATA_TYPE: "text",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: "Account Name",
          DISPLAY_ORDER: 1,
        },
        {
          COLUMN_NAME: "STATE",
          DATA_TYPE: "text",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: null,
          DISPLAY_ORDER: 2,
        },
        {
          COLUMN_NAME: "ACCOUNT_TYPE",
          DATA_TYPE: "text",
          SORTABLE: true,
          VISIBLE: true,
          DISPLAY_NAME: null,
          DISPLAY_ORDER: 3,
        },
        {
          COLUMN_NAME: "OWNER_NAME",
          DATA_TYPE: "text",
          SORTABLE: false,
          VISIBLE: true,
          DISPLAY_NAME: "OWNER_NAME",
          DISPLAY_ORDER: 4,
        },
        {
          COLUMN_NAME: "CREATED_AT",
          DATA_TYPE: "date",
          SORTABLE: false,
          VISIBLE: true,
          DISPLAY_NAME: null,
          DISPLAY_ORDER: 5,
        },
        {
          COLUMN_NAME: "STATUS_ID",
          DATA_TYPE: "select",
          SORTABLE: false,
          VISIBLE: true,
          DISPLAY_NAME: 'Status',
          DISPLAY_ORDER: 6,
        },
      ],
      TotalRecords: 0,
      PageNumber: 1,
      PageSize: 10,
      SortBy: "",
      SortExp: "",
      PageSizeOption: null,
      CurrentPage: 1,
      TotalPages: 0,
      GroupStart: 1,
      GroupEnd: 3,
      LeadStatusList:null,
      LeadOwnerList:null,
      Status: "",
      ModuleType: "",
      conditionForInstantSearch: "",
      LoggedinUserId:null,
      usertype: null,
      username: null,
      listActions: Schema.listActions,
      showAccountPopup:false,
      LegendArray: [],
      searchCondition: '',
    };
  },
  created:async function () {
    var ths = this;
      this.LoggedinUserId = this.GetUserInfo.ID
            this.username = this.GetUserInfo.Name
            this.usertype = this.GetUserInfo.UserType
            var ths = this;
   await Schema.bindleftCommonSearchdropdown(ths, "CRM", "CRMACCOUNTS");
   await Schema.bindleftCommonSearchdropdown(ths, "CRM", "clientAccType");
    ths.getStatuslistForListing("CRM", "en");
    ths.FetchData();
    // ths.LegendArray = [
    //         {
    //             class: 'legend-pending colorboxsquare',
    //             text: (ths.$t('CloseAccountLegend', ['Account']))
    //         }
    //     ]
  },
  methods: {
    UpdateClientStatus:function(id,statusId,type){
      var vm=this;
     var param=`id=${id}&statusId=${statusId}&type=${type}`;
      AccountsDataServices.UpdateStatusAccount(param).then(function(response){
       vm.ShowAlert(vm.$t("Status has been changed successfully."),"success", true, vm.$t("Alert"));
       vm.FetchData();
      });
    },
    clickEventOfColumn: function (row, column) {
      if (column.COLUMN_NAME == "CLIENT_NAME") {
        this.$router.push({
          path: "/CRM/ViewAccountDetails/" + row.USER_ID + "/CRM/CRM_ACCOUNTS/"+row.ACCOUNT_CODE,
        });
      }
    },
    renderActions: function(row) {
      var self = this;
      let rtnActionArray = [];
      self.listActions.forEach((actionItem) => {
        let action = { ...actionItem };
        switch (action.title) {
            case "Active":           
                action.isVisible =
              (self.isBlank(row.existing_client_id) ||
                self.isBlank(row.project_id)) &&
              (row.STATUS_ID == "1002" || row.STATUS_CODE == "NOT_VERIFIED");
            break;               
            case "Inactive":
                action.isVisible =
              (self.isBlank(row.existing_client_id) ||
                self.isBlank(row.project_id)) &&
              (row.STATUS_ID == "1001" || row.STATUS_CODE == "NOT_VERIFIED");
              break;        
            case "View":
            case "Edit":
            action.isVisible =
              (self.isBlank(row.existing_client_id) ||
                self.isBlank(row.project_id)) &&
              row.status_id != "Closed" &&
              row.status_id != "Cancelled";
            break;
          default: 
            action.isVisible = true;
            break;
          
        }
        rtnActionArray.push(action);
      });
      return rtnActionArray;
    },
    actionButtonClick:function(callbackfunction,item){
             
               switch(callbackfunction){
                case "ActiveClientStatus":
          this.UpdateClientStatus(item.USER_ID,1001,'Account');
          break;   
          case "InActiveClientStatus":
          this.UpdateClientStatus(item.USER_ID,1002,'Account');
          break;      
               }
              },
    commonHeaderButtonClick: function (event) {
      switch (event.callbackfunction) {
        case "openAddAccount":
          this.showAccountPopup=true;
          break;
        case "commonDeleteData":
          this.commonDeleteData();
          break;
          case "setTourGuideSteps":
                    this.setTourGuideSteps(0,this);
                        break;
          
      }
    },
    importAccount:function(){
      this.$router.push({name:'CRMAccounts'});
    },
    commonDeleteData: function (event) {                    
                    var vm = this;
                    var val = "";
                    $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                    if (val.length > 0) {
                        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("CRM_ACCOUNTS")), function () {
                            var postJSON = {
                                ids: val,
                                moduleName: "CRM",
                                subModuleCode: "CRM_ACCOUNTS"
                            };
                            var objectPostString = JSON.stringify(postJSON);
                            AccountsDataServices.CommonDeleteData(objectPostString).then(function (response) {    
                                var box = "";
                                if (response.status == 200) {
                                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                    box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('CRM_ACCOUNTS')]), "success", true, vm.$t("Alert"));
                                    $('#DeleteMultiple').addClass("disabled");
                                    //vm.PageNumber=parseInt(vm.PageNumber);
                                    if(vm.PageNumber<=0){
                                        vm.PageNumber=1;
                                    } 
                                     vm.FetchData(); 
                                }
                                else {
                                    box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('CRM_ACCOUNTS')]),'failure',true,vm.$t('Alert'));
                                }
                                setTimeout(function () {
                                    box.modal('hide');
                                }, 2000);
                                 
                            });
                        });
                    }
                    else {
                        vm.ShowAlert(vm.$t("Selectanyrecordtodelete"),'warning','failure',true,vm.$t('Alert'));
                    }                
    },
    pagerMethod: function (value) {
      this.PageSize = value.PageSize;
      this.PageNumber = value.PageNumber;
      this.FetchData();
    },
    leftCommonSearch: function (value) {
      this.PageNumber = value.PageNumber;
      this.isPaged = 1;
      if (typeof value.searchCondition != "undefined") {
        this.searchCondition = value.searchCondition;
      } else {
        this.searchCondition = "";
      }
      this.FetchData();
    },
    openFilterPopUp: function (source_id) {
      this.cancelOrRefresh();
      this.showFilter = true;
    },
    cancelOrRefresh: function () {
      this.clientemail = "";
      this.selectedLeadOwner = "";
      this.NameSearch = "";
      this.CompanyName = "";
      this.hdnOwnerId = "";
      this.conditionForInstantSearch = "";
      this.IsAnySearch = false;
      this.FetchData();
    },
    ManageView: function () {
      this.ShowManageView = true;
      setTimeout(function () {
        $("#modalManageView").modal("show");
        setTimeout(function () {
          SetBootstraptoolTip();
          0;
        }, 200);
      }, 1000);
    },
    sortdata: function (s) {
      var SortDir = "sort tb_headerSortDown";
      switch(s){
        case 'CLIENT_NAME':
          s='ACCOUNT_NAME'
          break;
          case 'STATE':
            s='TStat.name'
            break;
      }
      this.SortBy = s;
      if (s === this.SortBy) {
        if (this.SortExp == "") {
          this.SortExp = "ASC";
          SortDir = "sort tb_headerSortDown";
        } else if (this.SortExp == "ASC") {
          this.SortExp = "DESC";
          SortDir = "sort tb_headerSortUp";
        } else {
          this.SortExp = "ASC";
          SortDir = "sort tb_headerSortDown";
        }
      }

      this.FetchData();
      $("#" + s).attr("class", SortDir);
    },
    getRequestParams: function (pageNum, pageSize, sortBy, sortExp) {
      let params = {};
      if (pageNum) {
        params["pageNum"] = pageNum;
      }

      if (sortBy) {
        params["sortBy"] = sortBy;
      }
      if (sortExp) {
        params["sortExp"] = sortExp;
      }
      if (pageSize) {
        params["pageSize"] = pageSize;
      }
      return params;
    },
    FilterData: function (
      pageNum,
      pageSize,
      sortBy,
      sortExp,
      searchcondition,
      filterItemData
    ) {
      this.pageNum = pageNum;
      this.pageSize = pageSize;
      this.sortBy = sortBy;
      this.sortExp = sortExp;
      this.searchCondition = searchcondition;
      if (typeof filterItemData != null)
        this.appliedFilter.push(filterItemData);
      this.FetchData();
    },
    FetchData: function () {
      const params = this.getRequestParams(
        this.pageNum,
        this.pageSize,
        this.sortBy,
        this.sortExp,
        this.isPaged
      );
      var vueObj = this;
      this.isLoading = true;
      params.pageSize = vueObj.PageSize;
      params.pageNum = vueObj.PageNumber;
      params.searchCondition = vueObj.searchCondition;
      if (vueObj.PageNumber) {
        params.pageNum = vueObj.PageNumber;
      } else {
        vueObj.PageNumber = params.pageNum = 1;
      }
      params.sortBy = vueObj.SortBy;
      params.sortExp = vueObj.SortExp;
      var srchCondition = "";
      if (!this.isBlank(this.searchCondition)) {
        srchCondition = srchCondition + this.searchCondition;
      }
      if (!this.isBlank(this.conditionForInstantSearch)) {
        srchCondition =
          srchCondition +
          (!this.isBlank(this.searchCondition)
            ? " AND " + this.conditionForInstantSearch
            : this.conditionForInstantSearch);
      }
      if (srchCondition != "") {
        params.searchCondition = srchCondition;
        const removeFilterindex = vueObj.listheaderbuttons.findIndex(
          (object) => {
            return object.callbackfunction === "removeFilter";
          }
        );
        const openFilterPopUpindex = vueObj.listheaderbuttons.findIndex(
          (object) => {
            return object.callbackfunction === "openFilterPopUp";
          }
        );
      }
      AccountsDataServices.AccountList(params).then(function (response) {
        vueObj.isLoading = false;
        vueObj.Headers.forEach((column) => {
          column.settings = null;
          if (column.COLUMN_NAME == "CLIENT_NAME") {
            column.settings = {
              clickEvent: vueObj.clickEventOfColumn,
              formatter: vueObj.columnDataFormatter,
            };
          }
          if (column.COLUMN_NAME == "STATUS_ID") {
            column.settings = {
              isInSlot: true,
            };
          }
        });
        if (response.data != "" && response.data != null) {
          if (response.data.data.length > 0) {
            vueObj.AccountsData = response.data.data;
            vueObj.AccountsData.forEach((row)=>{
              row.rowDetailsUrl='';
              if(row.rowDetailsUrl=='')
              {
                  row.rowDetailsUrl="/CRM/ViewAccountDetails/" + row.USER_ID + "/CRM/CRM_ACCOUNTS/"+row.ACCOUNT_CODE;
              }
            });
            vueObj.TotalRecords = response.data.data[0].total_records;
            vueObj.TotalPages = Math.ceil(
              vueObj.TotalRecords / vueObj.PageSize
            );
            vueObj.CurrentPage = vueObj.PageNumber;
            vueObj.noRecord = false;
          } else {
            vueObj.noRecord = true;
            vueObj.AccountsData = [];
            vueObj.TotalRecords = 0;
          }
        } else {
          vueObj.noRecord = true;
          vueObj.AccountsData = [];
          vueObj.TotalRecords = 0;
        }
      });
      setTimeout(function () {
        vueObj.ResponsiveDataTable("tablelistingdata");
      }, 500);
      //vueObj.isLoading = false;
    },
    getStatuslistForListing: function (StatusType, lang) {
      var vm = this;
      var url =
        "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";

        AccountsDataServices.statusdropdown(url).then(function (response) {
        if (response.data.status == "Success") {
          var json = response.data.result;
          vm.StatusListForListing = json.data;
        }
      });
    },
    openAdditionalOwnerpopup: function () {
      this.showAdditionalOwnerPopup = true;
    },
  },
  updated: function () {
    this.BindActionButtonEvent();
  },
};
</script>
